@import url(https://fonts.googleapis.com/css2?family=Lora:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap);
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

@font-face {
  font-family: Muli;
  src: url(/my-portfolio/static/media/muli.light.86acb785.ttf);
}
body{
  /*background-color: rgb(255, 255, 255);*/
}
a{
  /* Resets */
  text-decoration: none;
  margin: 0;
  color: inherit;
  /* ------------- */
}
a:link {
  text-decoration: none;
}
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
  cursor: pointer;
}
a:active {
  text-decoration: none;
}
main{
  color: #444;
  font-family: 'Lora', serif;
  text-align: center;
  line-height: 1.8;
  padding: 30px 0;
}
main h1{
  font-size: 20px;
}
main p{
  font-family: 'Muli', sans-serif;
  font-size: 15px;
}
main a{
  font-size: 30px;
  padding: 20px;
  color: gray;
  transition: all .3s ease-in-out;
}
main a:hover{
  color: black
}
section{
  padding-bottom: 40px;
}
